@import url("https://fonts.googleapis.com/css?family=Varela+Round");
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
  letter-spacing: 1.1px;
}

body,
html {
  max-width: 1240px;
  margin: 0 auto;
  font-family: "Varela Round", sans-serif;
}
